              <div :class="request.status=='pending' ? 'disp-block' : 'disp-none'" class="custom-data-cell"><button class="basic-button cancel-btn" v-if="request.status=='pending' && type=='bonusAccount'" @click="cancelRequest(request, idx)">{{ $localizeFilter('cancel') }}</button></div>

