<script>
import TableOptions from "@/components/TableOptions.vue"
import localizeFilter from '@/filters/localize.filter'
import utils from '@/utils/utils'

export default {
  props: {
    requests: {
      type: Array,
      default: (() => {
          return []
      })
    },
    showTable: {
      type: Boolean,
      default: false
    },
    type: {
      type: String
    }
  },
  //  watch: { 
  //       requests: function(newVal, oldVal) {
  //           // console.log('???',newVal)
  //       }
  //   },
  data: () => ({
    statuses: ['all','pending', 'completed','canceled'],
    status: 'all'
  }),
  components: {
    TableOptions
  },
  methods: {
    selectOption(idx) {
      this.status = this.statuses[idx]
      this.$emit('changeStatus',this.status)
    },
    async cancelRequest(request, idx) {
       try {
          await this.$store.dispatch('cancelWithdrawBalanceRequest', {requestId: request.id})
          this.$message(localizeFilter('application_calceled'))
          this.$emit('cancelRequest',idx)
        } catch (e) {
             this.$error(e)
        }
    },
    displayCurrency(value) {
      let coinTicker = this.requests[0].coin
      return this.type=='bonusAccount' ? value : (+utils.displayBigIntAsDecimal(value, this.$store.getters.assetDecimalsMap[coinTicker], this.$store.getters.assetDecimalsMap[coinTicker].toString().length-1))
    },
  },
  computed: {
  },
}
</script>

